import { useEffect, useState } from "react"
import { useParams } from "react-router"
import network from '../../Scripts/network'
import url from '../../Scripts/url'
import SimpleImageSlider from "react-simple-image-slider";

let Event = () => {
    let { id } = useParams()
    let [event, setEvent] = useState(null)
    let [comment, setComment] = useState("")
    let [refresh, setRefresh] = useState(0)
    let [images, setImages] = useState(null)

    let generateComments = (comments) => {
        let out = []
        // console.log(images);
        for (let x in comments) {

            let curr = comments[x];

            out.push(<div className="comment-single" key={curr._id}>
                <h6>{curr.userid.username}</h6>
                <p>{curr.desc}</p>
            </div>)

        }
        out.reverse()
        return out;
    }


    useEffect(() => {
        network.getSingleEvent(id)
            .then((res) => {
                console.log(res.success);

                let img = res.success.images;

                let data = []
                if (img && img.length > 0) {
                    for (let x in img) {
                        data.push({ url: `${url}/${img[x]}` })
                    }
                }
                console.log(data);
                setEvent(res.success)
                setImages(data)

            })
    }, [id, refresh])

    if (!event) {
        return <div>Loading</div>
    }

    return <div>
        {images ? (() => {
            console.log('img', images);

            return <SimpleImageSlider
                width={896}
                height={504}
                images={images}
                showBullets={true}
                showNavs={true}
                autoPlay={true}
            ></SimpleImageSlider>
        })()
            :
            null
        }

        <h1>{event.title}</h1>
        <p>{event.desc}</p>
        <h3>About</h3>
        <p>{event.about}</p>

        {(() => {
            let images = []
            for (let x in event.images) {
                // console.log(event.images[x])
                images.push(<img alt="event" key={event.images[x]} src={`${url}/${event.images[x]}`} />)
            }
            return images
        })()}

        <form>
            <textarea value={comment} onChange={(e) => { setComment(e.target.value) }}></textarea>
            <input
                type="button"
                value="Submit"
                onClick={() => {
                    network.submitComment(comment, id)
                        .then(() => {

                            setRefresh(refresh + 1)
                        })
                }}
            ></input>
        </form>

        <div className="comments">
            {generateComments(event.comments)}
        </div>

    </div>
}

export default Event