import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import network from '../../Scripts/network'
import url from '../../Scripts/url'

let Home = () => {

    let [events, setEvents] = useState(null)
    let [page, setPage] = useState(1)

    let changePage = (diff) => {

        if (page + diff <= 0) {
            return;
        }

        setPage(page + diff)
    }

    useEffect(() => {
        console.log("ran effect");
        network.getEvents(page)
            .then((e) => {
                console.log(e.success.docs[0]);
                setEvents(e.success.docs);
            })
            .catch((err) => {
                console.log(err);
            })
    }, [page])


    if (!events) {
        return <h1>Loading</h1>
    }

    return <div >
        <div className='event-holder'>
            {
                (() => {
                    let eve = []
                    for (let x in events) {
                        // console.log(events[x]);
                        eve.push(<Link to={`/event/${events[x]._id}`} key={events[x]._id} className='event-card no-link'>
                            <img className='event-image' alt='event img' src={`${url}/${events[x]?.images[0]}`} />
                            <div className="event-text">
                                <h1 className='bold'>{events[x].title}</h1>
                                <p className='event-desc'>{events[x].desc}</p>
                            </div>
                        </Link>)
                    }
                    return eve
                })()

            }
        </div>
        <form className='page-controller'>
            <input type='button' value="prev" className='btn' onClick={() => { changePage(-1) }}></input>
            <h4 className='page'>{page}</h4>
            <input type='button' value="next" className='btn' onClick={() => { changePage(1) }}></input>
        </form>
    </div >
}

export default Home