import { useState } from "react";
import network from '../../Scripts/network'

let LoginSignUp = (props) => {
    let [login, setLogin] = useState(1);

    let [username, setUserName] = useState("");
    let [password, setPassword] = useState("");

    let saveToken = (token) => {
        localStorage.setItem('auth', token)
    }

    let auth = () => {

        if (login) {
            network.logIn(username, password)
                .then((res) => {
                    console.log(res);
                    if (res.success) {
                        saveToken(res.token)
                        props.setRefresh(props.refresh + 1)
                        console.log(props.refresh);
                    }

                })
        } else {
            console.log("sign - up");
            network.signUp(username, password)
                .then((res) => {
                    console.log(res);
                    if (res.success) {
                        saveToken(res.token)
                        props.setRefresh(props.refresh + 1)
                    }

                })
        }

    }

    return (
        <div>
            <form>
                <h2
                    onClick={() => {
                        setLogin(1)
                    }}
                >Login</h2>

                <h2
                    onClick={() => {
                        setLogin(0)
                    }}
                >Sign-up</h2>

                <p>Username</p>
                <input
                    value={username}
                    onChange={(e) => { setUserName(e.target.value); }}
                ></input>

                <p>Password</p>
                <input
                    value={password}
                    onChange={(e) => { setPassword(e.target.value) }}
                ></input>


                <input type='button' value="Submit" onClick={() => { auth() }} />
            </form>
        </div >
    )
}

export default LoginSignUp;