import url from './url'

let getEvents = (page) => {

    return fetch(url + '/event/page/' + page)
        .then((e) => {
            return e.json();
        })

}

let logIn = (username, password) => {
    let body = { username, password }

    return fetch(url + '/users/log-in', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(body)

    }).then((data) => {
        return data.json()
    })
}

let signUp = (username, password) => {
    let body = { username, password }

    return fetch(url + '/users/sign-up', {
        method: 'POST',
        headers: {
            'Content-type': 'application/json'
        },
        body: JSON.stringify(body)

    }).then((data) => {
        return data.json()
    })
}

let createEvent = (title, desc, about, images) => {

    //make seprate upload image 
    let userId = localStorage.getItem('auth')
    let body = { title, desc, about, images }

    return fetch(url + '/event/', {
        method: 'POST',
        headers: {
            'Content-type': 'application/json',
            'auth': userId
        },
        body: JSON.stringify(body)
    })
        .then((res) => {
            return res.json()
        })

}

let uploadEventImage = (img) => {

    let image = new FormData()
    image.append('image', img)

    let userId = localStorage.getItem('auth')

    return fetch(url + '/event/upload-event-image/', {
        method: 'POST',
        headers: {
            'auth': userId
        },
        body: image
    }).then((res) => {
        return res.json()
    })
}

let getSingleEvent = (id) => {
    return fetch(url + '/event/' + id)
        .then((res) => {
            return res.json()
        })
}

let submitComment = (desc, event) => {
    let userId = localStorage.getItem('auth')
    let body = { desc, event }

    return fetch(url + '/comment/', {
        method: 'POST',
        headers: {
            'Content-type': 'application/json',
            'auth': userId
        },
        body: JSON.stringify(body)
    })
        .then((res) => {
            return res.json()
        })
}

let getCurrentUser = () => {
    return fetch(url + '/users/get-current-user', {
        headers: {
            auth: localStorage.getItem('auth')
        }
    })
        .then((res) => {
            return res.json()
        })
}

let out = { getEvents, logIn, signUp, createEvent, getSingleEvent, submitComment, getCurrentUser, uploadEventImage }

export default out