import { useState } from "react"
import network from '../../Scripts/network'

let AddEvent = () => {
    let [title, setTitle] = useState("");
    let [desc, setDesc] = useState("");
    let [about, setAbout] = useState("");
    let [file, setFile] = useState("");
    let [images, setImages] = useState([])

    let fileChange = (e) => {
        console.log(images);
        setFile(e.target.files[0])
    }

    let submitEvent = () => {
        console.log(images);
        network.createEvent(title, desc, about, images)
            .then((res) => {
                console.log(res);
            })
    }

    let uploadImage = () => {
        network.uploadEventImage(file)
            .then((res) => {
                console.log("submit event : success : ", res.success);
                setImages([...images, res.success])
            })
    }

    return <div>
        <form>
            <h1>Add Event</h1>

            <h4>Title</h4>
            <input
                type='text'
                value={title}
                onChange={(e) => { setTitle(e.target.value) }}
            ></input>

            <h4>Desc</h4>
            <textarea
                value={desc}
                onChange={(e) => { setDesc(e.target.value) }}
            ></textarea>

            <h4>About</h4>
            <textarea
                value={about}
                onChange={(e) => { setAbout(e.target.value) }}
            ></textarea>

            <h4>Image</h4>
            <input type='file' onChange={fileChange} />
            <input type='button' value='uploadImage' onClick={uploadImage}></input>
            <div />

            <input type='button' value='submit' onClick={submitEvent}></input>

        </form>
    </div>

}

export default AddEvent