import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import network from '../../Scripts/network'

let Navbar = (props) => {

    let [user, setUser] = useState(null)
    let [refresh, setRefresh] = useState(0)

    useEffect(() => {
        network.getCurrentUser()
            .then((res) => {
                setUser(res)
            })
    }, [refresh, props.refresh])

    return <div className="navbar">
        <Link className='title no-link' to='/'>
            <div>Sports-Fista</div>
        </Link>


        {
            user && user.success ?
                <>
                    <div className="to-right username">
                        {user.success.username}
                    </div>

                    <Link to='/addevent' className="btn no-link">
                        +
                    </Link>


                    <div
                        className="login btn"
                        onClick={() => {
                            localStorage.setItem('auth', null)
                            setRefresh(refresh + 1)
                        }}
                    >
                        Logout
                    </div>
                </>
                :
                <Link to='/auth' className="login btn no-link to-right">
                    Login
                </Link>
        }
    </div>
}

export default Navbar